.testimonials__wrapper {
  padding: 10rem 0; }

@media (min-width: 575px) {
  .testimonials__slider {
    padding: 0 2rem; } }

.testimonials__slider .slick-next:before,
.testimonials__slider .slick-prev:before {
  font-size: 30px;
  line-height: 1;
  opacity: 0.75;
  color: #c50000; }

.testimonials__slider .slick-next:before:hover,
.testimonials__slider .slick-prev:before:hover {
  opacity: 1; }

.testimonials__slider__slide__quoteChar {
  font-size: 10rem;
  letter-spacing: 0;
  line-height: 0;
  font-family: didot; }

.testimonials__slider__slide__quote {
  font-size: 2rem;
  font-family: didot; }

.testimonials__slider__slide__customer {
  font-size: 12px;
  letter-spacing: 6px;
  text-transform: uppercase; }

@media (max-width: 950px) {
  .testimonials .slick-next:before,
  .testimonials .slick-prev:before {
    display: none; } }

.loading-animation {
  width: 100%;
  height: 100%;
  padding: 6rem 0;
  margin: 0 auto;
  text-align: center; }
  .loading-animation__icon .fa-spinner {
    width: 100px;
    height: 100px;
    margin: 0 auto;
    animation-name: loading-spinner;
    animation-duration: 1.3s;
    animation-iteration-count: infinite;
    animation-timing-function: ease-out; }

@keyframes loading-spinner {
  from {
    transform: rotateZ(0deg); }
  to {
    transform: rotateZ(360deg); } }
